@import "index.css";

body {
  background-color: #232323;
  font-family: Arial, sans-serif;
}

.wrapper {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

.block {
  margin-bottom: 10px;
}

.header {
  box-shadow: inset 0 10px 20px 0 #393b3f;
  background-color: #2c2e32;
  transition:all .5s ;
  border: 1px solid #3c3c3c;
  position: relative;
}

.header:after {
  right: 20px;
  top: 50%;
  transform: translate(0, -50%);
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.16);
  color: white;
  content: '+';
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header.active {
  box-shadow: inset 0 10px 20px 0 #aec456;
  background-color: #798f26;
  transition:all .5s ;
  border: 1px solid #3c3c3c;
}
.header.active .header__text {
  color: #ffffff;
  padding: 3px 10px;
  font-weight: 600;
  transition:all .5s ;
}

.header__text {
  color: #bcbcbc;
  padding: 3px 10px;
  font-weight: 600;
  transition:all .5s ;
  width: 90%;
}

.header__text_color {
  color: orange;
}

.header + .list {
  max-height: 0;
  overflow: hidden;
  transition-duration: .5s;
}

.header.active + .list {
  max-height: 1000px;
  transition-duration: .5s;
}

.list__item {
  box-shadow: inset 0 20px 5px 0 #292c2f;
  background-color: #212326;
  transition:all .5s ;
  border: 1px solid #7c7c7c;
  padding: 10px 30px;
  font-weight: 600;
  color: #cce1ec;
}

.link {
  box-shadow: inset 0 10px 20px 0 #9d9d9d;
  background-color: #b0b0b0;
  transition:all .5s ;
  border: 1px solid #3c3c3c;
  padding: 10px 60px;
  font-weight: 600;
  color: #dff4ff;
  position: relative;
}

.link__count  {
  height: 100%;
  width: 40px;
  background-color: #cccccc;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #626262;
}

.links  {
  max-height: 0;
  overflow: hidden;
  transition-duration: .5s;
}

.list__basic.active .links {
  max-height: 1000px;
  transition-duration: .5s;
}

.list__additional.active .links {
  max-height: 1000px;
  transition-duration: .5s;
}

.list__video.active .links {
  max-height: 1000px;
  transition-duration: .5s;
}
